 <template>
   <div class='ui fluid very long search selection ten column dropdown clearable '>
     <i class="dropdown icon"></i>
     <input class='search' v-model='searchText' ref='search'>
     <div class='default text'>
       Search...
    </div>
  </div>
</template>
 <script>
   module.exports={
     props:{
       value:{
         type:Object,
         default:{}
       }
     },
     data(){
       return{
         dat:null,
         searchText:''
       }
     },
     watch:{
      searchText:{
        handler(){
          this.$emit('search',this.searchText)
        }
      },
       value:{
        deep:true,
         handler(){
           $(this.$el).dropdown('setup menu',{
             values:this.values
           })
           this.searchText = ''
         }
       }
     },
     computed:{
       values(){
         return  _(this.value)
           .toPairs()
           .orderBy([0], 'asc')
           .fromPairs().reduce((o,c,k)=>{
              o.push({type:'header',name:k})
              o = o.concat(c)
              o.push({
                name:'dummy',
                value:'dummy'
              })
              return o
           },[])
       }
     },
     mounted(){
      /*
       $(this.$refs.search).on('keydown',(event)=>{
        switch(event.keyCode){
          case 13:
            alert('登録')
            break
        }
       })
       */
           var event = 'click.add'
           $(this.$el).off(event)
           $(this.$el).on(event,'#add',()=>{

             this.$emit('add',{
               name:this.searchText,
               value:this.searchText,
               image:`https://www.google.com/s2/favicons?domain=${this.searchText}`
             })
           })





       var dropdown = $(this.$el).dropdown({
        className: {
          selected    : 'selected',
          header      : 'ui label'
        },
        message: {
          noResults     : `

<div class="ui buttons" v-if='false'>
  <button class="ui button">削除</button>
  <div class="or"></div>
  <button id='add' class="ui positive button">登録</button>
</div>


          `
        },
         values:this.values,
         onChange:(value, text, $choice)=>{
            console.dir(value)
          this.$emit('search',value)
         },
         onLabelCreate:(value,text)=>{
           console.dir('onLabelCreate')
         },
         onLabelSelect:($selectedLabels)=>{
           console.dir('onLevelSelect')
         },
         onShow:()=>{



         },
         onAdd:(addedValue, addedText, $addedChoice)=>{
           console.dir('onAdd')
         },
         onSearch:async (text)=>{
           await this.$nextTick()
           var has = $('.item:not(.filtered)',this.$el).length > 0,
               $header = $('.menu > .label',this.$el)
               $header.removeClass('filtered')
               if (text.length){
                 $header.filter((i,node)=>!node.textContent.trim().startsWith(text[0])).addClass('filtered')
                 if (!has){
                   $header.addClass('filtered')
                 }
               }
         },
         onNoResults:(text)=>{

           return true
         }
       })
       $(this.$el).dropdown('setup menu',{
         values:this.values
       })







     }
   }
</script>
 <style>
 .ui.selection.dropdown[class*="very long"] .menu{
  padding:2px;
 }
  .ui.dropdown .menu > .item > .name {
    display: none;
  }
  .ui.dropdown .menu > .header{
    min-width: 100%;
  }
  .ui.dropdown > .text > img{
    display:none !important;
  }
  .ui.selection.dropdown .menu > .item {
    padding: 3px !important;
        display: flex;
    align-items: center;
    justify-content: center;
  }
  .ui.dropdown .ui.label:first-child{
    margin-left:0.142857em !important;
  }
  .ui.selection.dropdown[class*="very long"] .menu {
    max-height: 17em;
  }
  .ui.dropdown > .text > img, .ui.dropdown > .text > .image:not(.icon), .ui.dropdown .menu > .item > .image:not(.icon), .ui.dropdown .menu > .item > img {
    margin-right: 0;
    width: 20px;
    height: 20px;
}
.ui.selection.dropdown[class*="very long"] .menu::-webkit-scrollbar{
  display:none;
}
.ui.dropdown .filtered.label{
  display:none !important;
}
.ui.dropdown .ui.label{
  display:flex;
  align-items:center;
}
.selected {
  animation-name: action-bounce;
  animation-iteration-count: 1;
  animation-duration: 0.45s;
}
@keyframes action-bounce {
  0% {
    transform: translate(0px, 0px);
  }
  10% {
    transform: translate(0px, 2px);
  }
  50% {
    transform: translate(0px, -7px);
  }
  90% {
    transform: translate(0px, 4px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
.ui.dropdown .menu > .divider{
  width:100%;
}
.ui.selection.dropdown .menu > .item[data-value=dummy]{
    content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  padding:0 !important;
  line-height:0 !important;
  min-height:0 !important;
  height:2px !important;
  width:100% !important;
  overflow:hidden;
}
.ui.dropdown > .text > img, .ui.dropdown > .text > .image:not(.icon), .ui.dropdown .menu > .item > .image:not(.icon), .ui.dropdown .menu > .item > img{
  margin:0;
}

.ui.dropdown .label:first-child{
  border-top-right-radius:0.28571429rem !important;
  border-bottom-right-radius:0.28571429rem !important;
}

.ui.dropdown .menu .ui.label:nth-child(1){
    background-color: #f2711c;
    border-color: #f2711c;
    color: #fff;
}


.ui.dropdown .ui.label{
  font-weight:normal;
}
.ui.dropdown .ui.label {
    font-weight: normal;
    width: 27px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}


</style>